.list {

	.flex-list {
		@include flexbox;
		@include flex-wrap(wrap);
		margin: 0 0 -30px 0;
		width: calc(100% + 30px);
	}
 
	&__item {
		padding: 50px 45px;
		background: #f1f1f1;
		display: block;
		border-radius: 20px;
		width: calc(50% - 30px);
		margin:0 30px 30px 0;
		@include anim(all .2s ease);

		&:hover {
			background: darken(#f1f1f1, 3%);
		}
	}
}